import React from 'react';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import ReactDOM from 'react-dom';
import { HashRouter, Route, Link } from "react-router-dom";

ReactDOM.render(
  <HashRouter basename='/'>
    <Route path="/beebzdle" element={
      <App
        gameName="Beebzdle"
        customDictionary={["larby", "beebz", "poopy", "ricks"]}
        solution="RICKS"
      />
    } />
    <Route path="/discordle" element={
      <App
        gameName="Discordle"
        customDictionary={["weebs", "furry", "degen"]}
        solution="DEGEN"
      />
    } />
    <Route path="/kimatraidle" element={
      <App
        gameName="Kimatraidle"
        customDictionary={["anuja", "kiran", "aysha", "sindh"]}
        solution="SINDH"
      />
    } />
    <Route path="/thawardle" element={
      <App
        gameName="Thawardle"
        customDictionary={["hathi", "kirti", "radha", "kiran", "nanis", "dadis"]}
        solution="HATHI"
      />
    } />
  </HashRouter>,

  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
